<script>
  import { onMount } from "svelte";

  import {
    nicknames,
    fetchNicknames,
    nickname,
    mode,
  } from "./store.js";

  let nicknameexceptme = [];
  let myScore = 0;
  let winner;
  let winnerScore = 5;

  onMount(async () => {
    fetchNicknames($mode); // 닉네임을 가져옴
    nicknameexceptme = $nicknames.filter((item) => item !== $nickname);
  });

  async function submitrecord() {
  const data = {
    winner: winner,
    winnerScore: winnerScore,
    myScore: myScore,
  };
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert("다시 로그인해주세요");
    return;
  }

  if (myScore > 4 || myScore < 0) {
    alert("점수 입력이 잘못되었습니다");
    return;
  }

  const sendSubmitRequest = async () => {
    const response = await fetch("/submitrecord", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(data),
    });

    return response;
  };

  try {
    let response = await sendSubmitRequest();

    if (response.ok) {
      alert("기록 전송 완료");
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");

      response = await sendSubmitRequest(); // 토큰 갱신 후 다시 요청

      if (response.ok) {
        alert("기록 전송 완료");
      } else {
        alert(`오류 발생: ${response.status}`);
      }
    } else {
      alert(`오류 발생: ${response.status}`);
    }
  } catch (error) {
    console.error("오류 발생:", error);
    alert("오류 발생: " + error.message);
  }
}

  function handle_winner_change(event) {
    winner = event.target.value;
  }
</script>

<div class="left">
  승자 고르기
  <select
    bind:value={winner}
    on:change={handle_winner_change}
    class="namewidth"
  >
    {#each nicknameexceptme as option}
      <option value={option}>{option}</option>
    {/each}
  </select>
  내 점수 입력
  <input type="number" class="scorewidth" bind:value={myScore} />
  <button on:click={() => submitrecord()}>보내기</button>
</div>
